.mobile-nav {
    padding: 1.5rem 1.875rem;
    .logo {
        img {
            height: 2.25rem;
            filter: brightness(50);
        }
    }
    .bottom-nav {
        background-color: #ffffff0c;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        border-radius: 0.5rem 0.5rem 0 0;
        backdrop-filter: blur(10px);
        z-index: 9;
        ul {
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding: .65rem 1.5rem;
            li {
                width: 40px;
                a {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    img {
                        width: 2rem;
                        transform: translateY(10px);
                        filter: saturate(0);
                        transition: .5s;
                        scale: .85;
                    }
                    p {
                        margin-top: 3px;
                        font-size: 0.7rem;
                        font-weight: 500;
                        color: #38796b;
                        white-space: nowrap;
                        scale: 0;
                        line-height: 15px;
                        font-family: 'Changa', sans-serif;
                        transition: .5s;
                    }
                }
            }
            li.active {
                img {
                    filter: unset;
                    transform: translateY(0px);
                    scale: 1;
                }
                p {
                    scale: 1;
                    display: inline-block;
                }
            }
            .flow-bubbol {
                position: absolute;
                top: -20px;
                background: radial-gradient(#41c6c6, #235050);
                padding: .9rem 0.65rem;
                border-radius: 50%;
                box-shadow: 0 0 16px 16px #53c6c636;
                animation: flowBubbol 4s infinite alternate;
                transition: .5s;
            }
        }
    }
}

@keyframes flowBubbol {
    50% { 
        box-shadow: 0 0 16px 5px #53c6c636;
    }
    100% { 
        box-shadow: 0 0 16px 16px #53c6c636;
    }
}