header {
    .container {
        @media (min-width: 62rem) {
            & {
                display: flex;
            }
        }
        .header-text {
            width: 100%;
            margin-block: 4rem;
            @media (min-width: 62rem) {
                & {
                    width: 50%;
                    height: calc(100vh - 85px);
                    margin-block: 0;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: baseline;
                    transform: translateY(-10%);
                }
            }
            p, h1 {
                color: #fff;
                font-family: 'Bona Nova', serif;
            }
            p {
                font-size: 1.5rem;
                @media (min-width: 48rem) {
                    & {
                        font-size: 2.5rem;
                    }
                }
            }
            h1 {
                margin-block: 1.25rem;
                font-size: 2rem;
                line-height: 45px;
                @media (min-width: 48rem) {
                    & {
                        font-size: 3.125rem;
                        margin-block: 2rem;
                        line-height: 65px;
                    }
                }
            }
        }
        .header-img {
            @extend .center-g;
            width: 100%;
            min-height: 27.5rem;
            position: relative;
            padding-block: 3rem;
            @media (min-width: 62rem) {
                & {
                    position: unset;
                    width: 50%;
                    height: calc(100vh - 85px);
                }
            }
            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 100vw;
                height: 100%;
                background-color: #000;
                z-index: -1;
                @media (min-width: 62rem) {
                    & {
                        left: unset;
                        right: 0;
                        transform: unset;
                        width: 50vw;
                    }
                }
            }
            img {
                width: 90%;
                @media (min-width: 62rem) {
                    & {
                        width: 450px;
                        position: absolute;
                        transform: translateX(10%);
                    }
                }
                @media (min-width: 75rem) {
                    & {
                        width: 550px;
                        transform: translateX(4%);
                    }
                }
                @media (min-width: 93.75rem) {
                    & {
                        width: 650px;
                        transform: translateX(13%);
                    }
                }
            }
        }
    }
}

