button {
    background-color: #fff;
    font-size: 1rem;
    font-weight: 600;
    display: flex;
    align-items: center;
    border-radius: 3px;
    padding: .65em 1em;
    font-family: 'Bona Nova', serif;
    box-shadow: 0 2px 11px #ffffff78;
    cursor: pointer;
    transition: .5s;
    @media (min-width: 48rem) {
        & {
            font-size: 1.125rem;
        }
    }
    img {
        height: 1.25rem;
        margin-left: .3rem;
    }
    &:hover {
        background-color: transparent;
        color: #fff;
        outline: 1px solid #fff;
    }
    &:hover img {
        filter: invert(1);
    }
}