.desktop-nav {
    .container {
        display: flex;
        padding-block: 1.25rem;
        img {
            height: 2.813rem;
            margin-right: 6rem;
        }
        ul {
            width: 50%;
            display: flex;
            align-items: center;
            @media (min-width: 62rem) {
                & {
                    margin-left: auto;
                }
            }
            li {
                margin-inline: 1.25rem;
                transition: .5s;
                @media (min-width: 62rem) {
                    & {
                        margin-inline: 0.5rem;
                    }
                }
                @media (min-width: 75rem) {
                    & {
                        margin-inline: 1.5rem;
                    }
                }
                a {
                    color: #fff;
                    font-size: 0.875rem;
                    white-space: nowrap;
                    font-family: 'Changa', sans-serif;
                    position: relative;
                }
                a::after {
                    content: '';
                    position: absolute;
                    bottom: 5px;
                    left: 0;
                    height: 0.125rem;
                    width: 0;
                    background-color: #41c6c6;
                    transition: .5s;
                }
            }
            li:hover:not(li:last-child) a::after{
                width: 100%;
            }
            li:last-child {
                margin-left: 2.4rem;
                a {
                    color: #000;
                    background-color: #fff;
                    padding: .45rem 1.65rem;
                    border-radius: 0.313rem;
                    transition: .5s;
                }
                a:hover {
                    background-color: transparent;
                    color: #fff;
                    outline: 1px solid #fff;
                }
            }
            li:first-child {
                margin-left: 1.5rem;
            }
        }
    }
}